function saveToServer(levelArea) {
    fetch('http://localhost:3000', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({
            levelName: levelArea.levelName,
            tileMap: levelArea.tileMap
        }),
    }).catch(function () {
        alert('Could not save to server');
    });
}
export default saveToServer;
