import GAME_PLATFORM from 'game-platform';
import { EXPERIENCE_COMP, LEVEL_COMP, PLAYER_CONTROLLED_COMP } from 'components/ComponentNamesConfig';
import { EnemyKilledEvent } from "classes/GameEvents";
var Entity = GAME_PLATFORM.Entity;
function experienceSystem(systemArguments) {
    var gameEvents = systemArguments.gameEvents;
    var player = Entity.getByComps([PLAYER_CONTROLLED_COMP])[0];
    gameEvents.getEvents().forEach(function (event) {
        if (event instanceof EnemyKilledEvent) {
            var baseXP = 10;
            // enemy[LE]
            var currentXP = player[EXPERIENCE_COMP].XP;
            var newXP = event.entity[LEVEL_COMP].characterLevel * baseXP;
            player[EXPERIENCE_COMP].XP = currentXP + newXP;
        }
    });
}
export default experienceSystem;
